.BG__OVERLAY {
  opacity: 0.6;
  background-color: black;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 101;
}

.userLogs__modal {
  position: absolute;
  z-index: 1000;
  background-color: white;
  margin-top: 100px;
  margin-left: 400px;
  border-radius: 10px;
  width: 60vw;
  height: 60vh;
  padding-left: 85px;
  padding-top: 65px;
  padding-right: 600px;
}

.userLogs__modal .kruis {
  color: black;
  position: absolute;
  right: 50px;
  font-size: 1.7rem;
  cursor: pointer;
}

.userLogs__modal h2 {
  font-weight: 300;
  font-size: 1.8rem;
  margin-bottom: 40px;
}

.updateLog__update__velden form {
  display: flex;
  flex-direction: column;
}

.updateLog__update__velden input {
  width: 60%;
  margin-bottom: 20px;
  padding-left: 10px;
  padding-top: 5px;
  color: #797979;
  font-size: 0.9rem;
}

.updateLog__update__velden textarea {
  padding-left: 10px;
  padding-top: 5px;
  color: #797979;
  overflow-y: scroll;
  height: 200px;
  font-size: 0.9rem;
}

.buttons .btn {
  position: absolute;
  bottom: 40px;
  width: 140px;
  height: 50px;
  background-color: #4528d5;
  color: white;
  border: none;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  font-size: 1.3rem;
  font-weight: 300;
  transition: all 200ms;
}

.btn:hover {
  color: #4528d5;
  border: 1px solid #4528d5;
  background-color: white;
}
